@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,700;1,400&display=swap");
.live-chat .messages {
  padding: 20px 40px;
  background-color: #fff;
  height: 815px;
  max-height: 815px;
  overflow-y: auto;
  margin-bottom: 25px;
}
.live-chat .messages .image-wrap {
  max-width: 320px;
  padding: 15px 0 60px;
  margin-bottom: 15px;
  position: relative;
  display: inline-block;
}
.live-chat .messages .image-wrap .btn-download {
  padding: 4px 16px;
  display: flex;
  align-items: flex-start;
  position: absolute;
  right: 0;
  bottom: 0;
  font-size: 14px;
  font-family: "Lato";
}
.live-chat .messages .image-wrap .download-icon {
  margin-left: 15px;
}
.live-chat .messages .image-wrap.preview {
  padding: 15px 0;
  margin: 0;
}
.live-chat .messages .external-message {
  justify-content: flex-start;
}
.live-chat .messages .external-message .message {
  color: #000;
  background: #f7fbff;
}
.live-chat .messages .external-message .message-date {
  margin-left: 5px;
}
.live-chat .messages .internal-message {
  justify-content: flex-end;
}
.live-chat .messages .internal-message .message {
  color: #fff;
  background-color: #0a2c68;
}
.live-chat .messages .internal-message .username {
  text-align: end;
}
.live-chat .messages .internal-message .message-date {
  margin-right: 5px;
}
.live-chat .messages .text-message {
  display: flex;
}
.live-chat .messages .text-message .message-wrap {
  padding: 20px 0;
  font-family: "Roboto";
}
.live-chat .messages .text-message .message {
  padding: 14px 20px;
  width: 445px;
  max-width: 445px;
  font-size: 18px;
  font-weight: 700;
  border-radius: 30px;
}
.live-chat .messages .text-message .username {
  margin-top: 10px;
  font-style: italic;
  color: #002952;
}

.user-message-wrap {
  flex: 1 1 auto;
  position: relative;
  background-color: #fff;
  display: flex;
}
.user-message-wrap .user-message {
  flex: 1 1 auto;
  height: 75px;
  border: none;
  background-color: transparent;
  padding: 0 30px;
  outline: none;
  color: #002952;
  font-size: 18px;
  font-weight: 500;
}
.user-message-wrap .user-message::placeholder {
  font-weight: 300;
  font-size: 18px;
  color: #002952;
}
.user-message-wrap .control-btns {
  display: flex;
}
.user-message-wrap .upload-image {
  padding: 0 15px;
  position: relative;
  display: flex;
  align-items: center;
  font-size: 10px;
  font-style: italic;
  background-color: transparent;
  border: none;
  outline: none;
}
.user-message-wrap .upload-image::before {
  content: "";
  position: absolute;
  right: 0;
  top: 12px;
  bottom: 12px;
  width: 1px;
  background-color: #b7b7b7;
}
.user-message-wrap #preview-image {
  width: 30px;
  height: 30px;
  margin-left: 10px;
  display: none;
}
.user-message-wrap .upload-icon {
  margin-left: 10px;
}
.user-message-wrap .enter-message {
  padding: 0 15px;
  display: flex;
  align-items: center;
  background-color: transparent;
  border: none;
  outline: none;
}
.user-message-wrap .enter-message:hover .cricle {
  filter: brightness(120%);
}
.user-message-wrap .enter-message .cricle {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 58px;
  height: 58px;
  background-color: #0a2c68;
  border-radius: 50%;
  transition: all 0.2s ease-in-out;
}

.message-layout .attention {
  max-width: 150px;
  font-size: 12px;
  color: #8c8c8c;
  margin-top: 20px;
}

.message-layout.confirm-email .confirm-email-message {
  padding: 10px;
}

.file-size-invalid {
  display: none;
  position: absolute;
  bottom: 3px;
  width: 300px;
  right: -50px;
  font-size: 12px;
  color: #a5321a;
}

.error-icon {
  width: 26px;
  margin-right: 10px;
}

@media (max-width: 991px) {
  .live-chat .messages .text-message .message {
    padding: 10px 20px;
    font-size: 14px;
    width: 320px;
    border-radius: 20px;
  }
}
@media (max-width: 575px) {
  .message-layout {
    background-color: #f6f9ff;
    padding: 0 10px 10px;
  }

  .message-layout.no-auth {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    background-color: transparent;
    padding: 0;
    margin-top: 30px;
  }

  .live-chat {
    padding: 10px;
    background-color: #f6f9ff;
  }
  .live-chat .messages {
    margin-bottom: 0;
    padding: 20px 15px;
  }
  .live-chat .messages .text-message .message-wrap {
    width: 100%;
  }
  .live-chat .messages .text-message .message {
    width: 100%;
  }
  .live-chat .messages .image-wrap {
    padding: 15px 0 40px;
  }

  .user-message-wrap .upload-image {
    min-width: 55px;
    padding: 0 5px;
  }
  .user-message-wrap .upload-image::before {
    display: none;
  }
  .user-message-wrap .enter-message {
    padding: 0 15px 0 7px;
  }
  .user-message-wrap .enter-message .cricle {
    width: 42px;
    height: 42px;
  }
  .user-message-wrap .enter-message .cricle img {
    width: 10px;
  }
  .user-message-wrap .user-message {
    width: 115px;
    padding: 0 10px;
    font-size: 16px;
  }
  .user-message-wrap .user-message::placeholder {
    font-size: 16px;
  }

  .live-chat {
    padding: 10px;
    background-color: #f6f9ff;
  }

  .modal-wrap {
    text-align: center;
    margin-top: 30px;
  }
}
.modal-dialog {
  max-width: 365px;
  margin: 18rem auto;
}

.form-control {
  padding: 5px 10px;
  height: 34px;
  margin-bottom: 15px;
  border-radius: 0;
  border-color: #ddd;
}
.form-control::placeholder {
  color: #ddd;
  font-size: 14px;
}
.form-control:nth-child(2) {
  margin-bottom: 40px;
}

.btn-modal {
  position: relative;
  padding: 6px 24px;
  font-size: 18px;
}

.modal-rules {
  max-width: 1240px;
  margin: 3rem auto;
}

.modal-content-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #12293C;
  padding: 25px 25px 0;
}
.modal-content-wrap .go-live-stream {
  margin: 30px 0;
}

.rules-comntent {
  max-height: 650px;
  overflow: auto;
}

.go-live-stream {
  text-align: start;
  min-width: 325px;
  display: inline-flex;
  align-items: center;
  font-size: 24px;
  padding: 8px 15px 8px 25px;
  text-transform: uppercase;
}
.go-live-stream::after {
  content: "";
  margin-left: 20px;
  border: 12px solid transparent;
  border-left: 20px solid #fff;
}

.stream {
  background-color: #F6F9FF;
  padding-bottom: 50px;
}
.stream h2 {
  font-size: 36px;
  font-weight: 500;
  margin: 0;
  padding: 40px 0;
  text-align: center;
  text-transform: uppercase;
}

.stream-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}
.stream-wrap .live-stream {
  width: 355px;
  text-transform: uppercase;
  font-size: 24px;
}

.go-to-chat {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem 1rem;
  margin-bottom: 30px;
  background-color: #fff;
}
.go-to-chat .form-control {
  max-width: 300px;
}
.go-to-chat .btn-red {
  margin-bottom: 15px;
}

.accent {
  color: #A5321A;
}

@media (min-width: 992px) {
  .stream-wrap .live-stream {
    position: absolute;
    bottom: 45px;
  }
}
@media (max-width: 991px) {
  .stream-wrap .live-stream {
    margin-top: 40px;
  }

  .go-live-stream {
    min-width: 305px;
    font-size: 22px;
  }
}
@media (max-width: 575px) {
  .stream {
    background-color: #fff;
    padding-bottom: 30px;
  }
  .stream h2 {
    padding: 30px 0;
  }

  .stream-wrap .live-stream {
    width: 100%;
  }

  .go-live-stream {
    min-width: 100%;
    font-size: 20px;
    padding: 8px 0;
    justify-content: center;
  }
  .go-live-stream::after {
    margin-left: 15px;
    border: 10px solid transparent;
    border-left: 16px solid #fff;
  }
}